@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
html{font-size:10px}body{margin:0;overflow-x:hidden;font-family:Inter;color:#1C1C21}*{box-sizing:border-box;outline:none}@media (min-width: 1921px){html{font-size:15px}}

.game-start{display:flex;align-items:center;width:100%;height:100vh}.game-start_bg{background:linear-gradient(to top left, #fff3eb 50%, #fff 50%)}.game-start__wrapper{display:flex;align-items:center;justify-content:space-between;width:110rem;margin:0 auto}.game-start__logo{width:100%;max-width:45rem}.game-start__content{display:flex;flex-direction:column}.game-start__title{font-size:5.6rem;font-style:normal;font-weight:600;line-height:6.5rem;max-width:52.5rem;margin-top:0;margin-bottom:6.5rem}.game-start__score-title{font-size:3.2rem;font-style:normal;font-weight:600;line-height:3.7rem;color:#97979b;margin:0}.game-start__button{font-size:2rem;font-style:normal;font-weight:600;line-height:2.3rem;text-align:center;color:#ffffff;background-color:#ff8b37;border:none;border-radius:1.2rem;padding:2rem 2.4rem;transition:all 0.3s;width:29.6rem}.game-start__button:hover{background-color:#ffac70}.game-start__button:active{background-color:#e87928}@media screen and (max-width: 1439px) and (min-width: 992px){.game-start__wrapper{width:80rem}}@media screen and (max-width: 991px){.game-start__wrapper{flex-direction:column;justify-content:space-between;height:100%;padding:5rem 1.5rem}.game-start__logo{max-width:19.6rem;flex:50% 1}.game-start__content{align-items:center;justify-content:space-between;flex:50% 1}.game-start__title{font-size:3.2rem;line-height:3.712rem;text-align:center}}@media screen and (max-width: 767px){.game-start__button{font-size:1.4rem;line-height:1.6rem;border-radius:0.8rem;padding:1.6rem 2.4rem;width:28.8rem}}

.game-field{background:#e5e5e5;height:100vh}.game-field__wrapper{display:flex;justify-content:space-between;height:100%}

.mobile-menu{position:absolute;top:20px;right:20px}.mobile-menu__button{border:none;padding:0}@media screen and (min-width: 992px){.mobile-menu{display:none}}

.game-question{display:flex;flex-direction:column;justify-content:space-between;flex:0 0 75%;padding:10.5rem 8rem 8.5rem}.game-question__title{font-size:3.2rem;font-style:normal;font-weight:600;line-height:3.7rem;max-width:70rem}.game-question__answers{display:flex;flex-wrap:wrap;max-width:84rem}.game-question__button{font-size:2rem;font-style:normal;font-weight:400;line-height:2.3rem;background-size:100% 100%;padding:2.6rem 3.3rem;background-image:url(../../static/media/answer-inactive.157d72db.svg);text-align:left;position:relative;border:none;background-color:transparent;width:39rem;margin:0 1.5rem 3.5rem}.game-question__button-letter{color:#ff8b37;margin-right:1rem}.game-question__button:before,.game-question__button:after{content:"";display:block;width:1.7rem;top:calc(50% - 0.1rem);position:absolute;height:0.1rem;background-color:#d0d0d8}.game-question__button:before{left:-1.7rem}.game-question__button:after{right:-1.7rem}.game-question__button:hover{background-image:url(../../static/media/answer-hover.226e9422.svg)}.game-question__button:hover:before,.game-question__button:hover:after{background-color:#ff8b37}.game-question__button_selected,.game-question__button_selected:hover{background-image:url(../../static/media/answer-selected.7e5aaa8b.svg)}.game-question__button_selected:before,.game-question__button_selected:after,.game-question__button_selected:hover:before,.game-question__button_selected:hover:after{background-color:#ff8b37}.game-question__button_correct,.game-question__button_correct:hover{background-image:url(../../static/media/answer-correct.29122387.svg)}.game-question__button_correct:before,.game-question__button_correct:after,.game-question__button_correct:hover:before,.game-question__button_correct:hover:after,.game-question__button_correct:hover:before,.game-question__button_correct:hover:after,.game-question__button_correct:hover:hover:before,.game-question__button_correct:hover:hover:after{background-color:#47d867}.game-question__button_wrong,.game-question__button_wrong:hover{background-image:url(../../static/media/answer-wrong.9948cae0.svg)}.game-question__button_wrong:before,.game-question__button_wrong:after,.game-question__button_wrong:hover:before,.game-question__button_wrong:hover:after,.game-question__button_wrong:hover:before,.game-question__button_wrong:hover:after,.game-question__button_wrong:hover:hover:before,.game-question__button_wrong:hover:hover:after{background-color:#ec6259}@media screen and (max-width: 1439px) and (min-width: 992px){.game-question__button{padding:1.35rem 3.3rem;width:26rem}}@media screen and (max-width: 991px){.game-question{flex:0 0 100%;padding:13.6rem 0 2.4rem;align-items:center}.game-question__title{text-align:center}.game-question__answers{justify-content:center}.game-question__button{width:28.8rem;margin:0 1.5rem 0.8rem;padding:1.65rem 3.3rem}}@media screen and (max-width: 767px){.game-question__title{font-size:1.8rem;line-height:2.088rem;max-width:28.8rem}}

.game-steps{display:flex;background:#ffffff;flex:0 0 25%;overflow-x:hidden;padding:6.4rem 0 3.2rem;justify-content:flex-start}.game-steps__list{display:flex;width:100%;height:100%;align-items:center;justify-content:center;flex-direction:column;background:#ffffff;overflow-x:hidden}.game-steps__step{font-size:2rem;font-style:normal;font-weight:400;line-height:2.3rem;text-align:center;width:24rem;padding:0.9rem 1rem 0.8rem;position:relative;background-size:100% 100%}.game-steps__step:not(:last-child){margin-bottom:0.8rem}.game-steps__step:before,.game-steps__step:after{content:"";display:block;width:100%;top:calc(50% - 0.1rem);position:absolute;height:0.1rem;background-color:#d0d0d8}.game-steps__step:before{left:-100%}.game-steps__step:after{right:-100%}.game-steps__step_inactive{background-image:url(../../static/media/step-inactive.2bd1f67a.svg);color:#1C1C21}.game-steps__step_inactive:before,.game-steps__step_inactive:after{background-color:#d0d0d8}.game-steps__step_current{background-image:url(../../static/media/step-current.e3708608.svg);color:#ff8b37}.game-steps__step_current:before,.game-steps__step_current:after{background-color:#ff8b37}.game-steps__step_finished{background-image:url(../../static/media/step-finished.2bd1f67a.svg);color:#d0d0d8}.game-steps__step_finished:before,.game-steps__step_finished:after{background-color:#d0d0d8}@media screen and (max-width: 991px){.game-steps{position:absolute;width:100%}.game-steps_mobile{display:none}}@media screen and (max-width: 767px){.game-step__step{padding:0.4rem 1rem 0.5rem}.game-step__step_inactive{background-image:url(../../static/media/step-inactive-mobile.5909fa69.svg)}.game-step__step_current{background-image:url(../../static/media/step-current-mobile.60bd6ac3.svg)}.game-step__step_finished{background-image:url(../../static/media/step-finished-mobile.5909fa69.svg)}}

